html,body{max-width:100%;overflow-x:hidden}.canvas{margin-left:30px;position:relative;top:-50px}.modal{font-family:'Indie Flower', cursive;display:none;position:absolute;z-index:1;left:0;top:-100px;width:100%;height:150%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.4);overflow-y:hidden}.github{bottom:70px;font-size:20px;font-family:'Indie Flower', cursive;text-decoration:none;position:relative;left:1000px;padding-top:12px;padding-bottom:12px;padding-left:20px;padding-right:20px;background-color:aqua;border-radius:10px;color:blue}.linkden{font-size:20px;font-family:'Indie Flower', cursive;text-decoration:none;position:relative;bottom:70px;left:900px;padding-top:12px;padding-bottom:12px;padding-left:20px;padding-right:20px;background-color:aqua;border-radius:10px;color:blue}#myBtn{position:relative;bottom:70px;right:150px;font-size:20px;font-family:'Indie Flower', cursive;border-radius:10px;height:54px;width:120px;background-color:aqua;color:blue;border:none}#reset-btn{position:relative;bottom:70px;right:90px;font-size:20px;font-family:'Indie Flower', cursive;border-radius:10px;height:54px;width:120px;background-color:aqua;color:blue;border:none}.title{margin:0px;color:indianred;position:relative;left:400px;top:0px;font-family:'Indie Flower', cursive;font-size:420%}.modal-content{border-radius:20px;color:indianred;font-weight:bolder;font-size:25px;font-family:'Indie Flower', solid;background-color:white;margin:10% auto;padding:20px;border:1px solid #888;width:80%}.close{position:relative;bottom:20px;color:#bd1b1b;float:right;font-size:48px;font-weight:bold}.close:hover,.close:focus{color:black;text-decoration:none;cursor:pointer}
